
export class PlayerInfo {
    ffgNumber: string;
    prenom: string;
    nom: string;
    circuit: string;
    civilite: string;
    categorie: number;
    nbrRoundThisYear: string;
    email: string;
    phoneNumber: string;
    comment: string;
    date: string;
    twin: boolean = false;

    public build(ffgNumber, prenom, nom, civilite, circuit,  categorie, nbrRoundThisYear, email, phoneNumber, comment, date): PlayerInfo {
        return  new PlayerInfo()
                    .setFfgNumber(ffgNumber)
                    .setPrenon(prenom)
                    .setNom(nom)
                    .setCircuit(circuit)
                    .setCivilite(civilite)
                    .setCategorie(categorie)
                    .setNbrRoundThisYear(nbrRoundThisYear)
                    .setEmail(email)
                    .setPhoneNumber(phoneNumber)
                    .setComment(comment)
                    .setDate(date)
                    .setTwin(false)
                    ;
    }

    public setDate(date): PlayerInfo {
        this.date = date;
        return this;
    }

    public setComment(comment): PlayerInfo {
        this.comment = comment;
        return this;
    }

    public setPhoneNumber(phoneNumber): PlayerInfo {
        this.phoneNumber = phoneNumber;
        return this;
    }

    public setEmail(email): PlayerInfo {
        this.email = email;
        return this;
    }

    public setFfgNumber(ffgNumber): PlayerInfo {
        this.ffgNumber = ffgNumber;
        return this;
    }

    public setPrenon(prenom): PlayerInfo {
        this.prenom = prenom;
        return this;
    }

    public setNom(nom): PlayerInfo {
        this.nom = nom;
        return this;
    }

    public setCircuit(circuit): PlayerInfo {
        this.circuit = circuit;
        return this;
    }

    public setCategorie(categorie): PlayerInfo {
        this.categorie = categorie;
        return this;
    }

    public setNbrRoundThisYear(nbrRoundThisYear): PlayerInfo {
        this.nbrRoundThisYear = nbrRoundThisYear;
        return this;
    }

    public setCivilite(civilite): PlayerInfo {
        this.civilite = civilite.replace('Garçon', 'M').replace('Fille', 'F');
        return this;
    }

    public setTwin(twin): PlayerInfo {
        this.twin = twin;
        return this;
    }

}
