import { Component, OnInit } from '@angular/core';
import {NavbarService} from '../../_shared/services/navbar.service';
import {AuthenticationService} from '../../_shared/services/authentication.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  isLoggedIn = false;
  role = '';

  public username: string = undefined;
  public password: string = undefined;

  constructor(private navbarService: NavbarService, private authenticationService: AuthenticationService) {
    this.navbarService.getLoginStatus().subscribe(status => this.isLoggedIn = status);

  }

  ngOnInit() {
  }

  verfiyLogin() {
    this.loginAdmin();
  }

  loginUser() {
    this.navbarService.updateNavAfterAuth('user');
    this.navbarService.updateLoginStatus(true);
    this.role = 'user';
  }

  loginAdmin() {
    this.navbarService.updateNavAfterAuth('admin');
    this.navbarService.updateLoginStatus(true);
    this.role = 'admin';
  }

}
