import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {NavbarService} from './_shared/services/navbar.service';
import {AuthenticationService} from './_shared/services/authentication.service';
import {AtelierComponent} from './_modules/atelier/atelier.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'dataGolf';

  links: Array<{ text: string, path: string }>;
  isLoggedIn = false;

  public logged = false;
  public username: string;
  public password: string;

  constructor(private router: Router, private navbarService: NavbarService, private authenticationService: AuthenticationService) {
    this.router.config.unshift(
      /* { path: 'login', component: LoginComponent },*/
      /* { path: 'user', component: UserComponent },
       { path: 'admin', component: AdminComponent },*/
      { path: 'atelier', component: AtelierComponent },
    );
  }

  ngOnInit() {
    this.links = this.navbarService.getLinks();
    this.navbarService.getLoginStatus().subscribe(status => this.isLoggedIn = status);
    if (this.logged === false){
      this.router.navigate(['home']);
    }
  }

  logout() {
    this.navbarService.updateLoginStatus(false);
    this.router.navigate(['home']);
  }

  verfiyLogin() {
    if (this.authenticationService.verification(this.username, this.password)) {
      this.authenticationService.loginAdmin();
      this.logged = true;
    } else {
      alert('Probleme d\'authentification!');
    }
  }
}
