import {Component, Inject, Input, OnInit} from '@angular/core';
import {PlayerInfo} from '../../_shared/models/player-info.model';
import {DOCUMENT} from '@angular/common';

@Component({
  selector: 'app-header-tableau',
  templateUrl: './header-tableau.component.html',
  styleUrls: ['./header-tableau.component.css']
})
export class HeaderTableauComponent implements OnInit {

  @Input() currentcategorie: number;
  @Input() tabPlayersInfos: PlayerInfo[] = [];
  @Input() printHeader = false;
  @Input() printAreaBottom: number ;
  @Input() logo: string ;

  constructor(@Inject(DOCUMENT) private document: Document) { }

  ngOnInit() {
  }

  /* uppercas string parameter */
  stringToCircuit(maChaine): string {
    return maChaine.replace('BFGT', 'Découvertes').toLowerCase().replace('bt\'s', '').trim();
  }

  cleanString(input): string{
    const re = /(?![\x00-\x7F]|[\xC0-\xDF][\x80-\xBF]|[\xE0-\xEF][\x80-\xBF]{2}|[\xF0-\xF7][\x80-\xBF]{3})./g;
    return input.replace(re, '');
  }

  /* uppercas string parameter */
  stringToNom(maChaine): string {
    return this.cleanString(maChaine).toLowerCase()
                  .replace('?', '-')
                  .replace('- -', '-')
                  .replace('�', '').trim();
  }



}
